import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';

/*
  Generated class for the MiembrosProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class MiembrosProvider {

  constructor(public storage: Storage) {
  }

  obtenerMiembros(){
    return this.storage.get('miembros').then(miembros=>{
      // if(!miembros) miembros = [];
      // return miembros;
      return [
        {
            nombre:"Pablo Rivas",
            descripcion:""
        },
        {
            nombre:"Pablo Bertorello",
            descripcion:""
        },
        {
            nombre:"El Eze",
            descripcion:""
        },
        {
            nombre:"Nazareno Medrano",
            descripcion:""
        },
        {
            nombre:"Emma Fuentes",
            descripcion:""
        },
        {
            nombre:"Diego Prystupczuk",
            descripcion:""
        },
        {
            nombre:"Alejandro Venturini",
            descripcion:""
        },
        {
            nombre:"Gustavo Sequeira",
            descripcion:""
        },
        {
            nombre:"Mati Ogo",
            descripcion:""
        },
        {
            nombre:"Fedra",
            descripcion:""
        },
        {
            nombre:"Walter",
            descripcion:""
        },
        {
            nombre:"Diego López",
            descripcion:""
        },
        {
            nombre:"Guillermo Peralta ",
            descripcion:""
        },
        {
            nombre:"Joseph Araya",
            descripcion:""
        },
        {
            nombre:"Matías Barrionuevo",
            descripcion:""
        },
        {
            nombre:"Carlos Andrés Taborda",
            descripcion:""
        },
        {
            nombre:"Lucas Riveros",
            descripcion:""
        },
        {
            nombre:"Fernán Torres",
            descripcion:""
        },
        {
            nombre:"Mauricio",
            descripcion:""
        },
        {
          nombre: "Malka",
          descripcion: ""
        }
    ]
    });
  }

  crearMiembro(miembro){
    return this.obtenerMiembros().then(miembros=>{
      miembros.push(miembro);
      return this.storage.set('miembros', miembros);
    })
  }

}



